import { LoadingController, NavController } from "@ionic/angular";
import { ActivatedRoute, Router } from "@angular/router";
import { UserService } from "./../../services/user.service";
import { Component, OnInit, TemplateRef } from "@angular/core";
import { PlansService } from "./../../services/plans.service";
import { DietService } from "./../../services/diet.service";
import { NgIfContext } from "@angular/common";

@Component({
  selector: "app-diet-details",
  templateUrl: "./diet-details.page.html",
  styleUrls: ["./diet-details.page.scss"],
})
export class DietDetailsPage implements OnInit {
  labelSnack: string;
  mesAtual: string = '';
  dieta: any;
  programa: string = '';
  slideOpts = {
    initialSlide: 1,
    speed: 400,
  };
skeletonText: TemplateRef<NgIfContext<string>>;

  constructor(
    private userSrv: UserService,
    private router: Router,
    private dietSrv: DietService,
    private planSrv: PlansService,
    private loadCtrl: LoadingController,
    private actvRouter: ActivatedRoute,
    private navCtrl: NavController
  ) {}

  ngOnInit() {
    this.actvRouter.queryParams.subscribe((data) => {
      this.loadDiet(data.dia, data.refeicao);
      this.setLabelSnack(data.refeicao);
    });
  }

  async loadDiet(diaSemana, refeicao) {
    this.loadInfo();
    const loading = await this.loadCtrl.create({ message: "Carregando" });

    try {
      const monthPlan = await this.planSrv.getMonthPlan().toPromise();
      const dia = diaSemana;
      const dadosDieta = {
        mes: monthPlan.mes_atual,
        dia: dia,
        refeicao: refeicao,
      };

      const dietData = await this.dietSrv
        .getDietUserDay(dadosDieta)
        .toPromise();

      loading.present();
      this.dieta = dietData.dados;
      console.log(this.dieta);
    } catch (error) {
      console.error("Erro ao carregar a dieta:", error);
    } finally {
      loading.dismiss();
    }
  }

  loadInfo() {
    this.planSrv.getMonthPlan().subscribe((data) => {
      console.log(data);
      this.mesAtual = data.mes_atual;
    });
    this.userSrv.getUserPlan().subscribe((response) => {
      this.programa = response.programa;
      console.log(this.programa);
    });
  }

  setLabelSnack(refeicao: string) {
    switch (refeicao) {
      case "1":
        this.labelSnack = "Pre-Treino - 30 minutos antes do treino";
        break;
      case "2":
        this.labelSnack = "Café da manhã - 8 horas";
        break;
      case "3":
        this.labelSnack = "Almoço - 12 horas";
        break;
      case "4":
        this.labelSnack = "Lanche da Tarde - 16 horas";
        break;
      case "5":
        this.labelSnack = "Jantar - 20 horas";
        break;
      default:
        this.labelSnack = "Refeição Desconhecida";
    }
  }

  backToSnackWeek() {
    this.router.navigate(["week-diet"]);
  }
}
